<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级作息设置</p>
        </div>
        <div class="content f28 c333">
            <van-tabs v-model="active" type="card" color='#6b77ff' :sticky="true" class="restClass">
                <van-tab title="设置">
                    <div class="chooseDate">
                        <p>选择日期</p>
                        <div class="chooseBox">
                            <input type="text" placeholder="请选择开始时间" v-model="model1" class="chooseInp l" @click="changeModel(1)" readonly>
                            <div class="l lineDiv"></div>
                            <input type="text" placeholder="请选择结束时间" v-model="model2" class="chooseInp l" @click="changeModel(2)" readonly>
                        </div>
                        <p>选择日期</p>
                        <div class="chooseBox">
                            <input type="text" placeholder="请选择签到时间" v-model="model3" class="chooseInp l" @click="changeModel(3)" readonly>
                            <div class="l lineDiv"></div>
                            <input type="text" placeholder="请选择签退时间" v-model="model4" class="chooseInp l" @click="changeModel(4)" readonly>
                        </div>
                    </div>
                    <div class="centerBtn f26 cfff text-center" @click="saveOrUpdateClassesTable">确认创建</div>
                </van-tab>
                <van-tab title="历史记录">
                    <div class="list-for">
                        <div class="list-item" v-for="(item,index) in history" :key="index">
                            <div class="item-left l">
                                <div class="dateYear f22 c333 mt-30">{{item.startTimeTable}} 至 {{item.endTimeTable}}</div>
                                <div class="dateTime f20 mt-30">{{item.startSignTime}} - {{item.endSignTime}}</div>
                            </div>
                            <div class="item-right r" @click="saveOrUpdateClassScheduleRecord(item.id)">
                                删除
                            </div>
                        </div>
                    </div>
                </van-tab>
            </van-tabs>
        </div>
        <van-popup v-model="show" position="bottom">
            <van-datetime-picker
                @confirm="confirm"
                @cancel = "cancel"
                v-model="currentDate"
                type="date"
            />
        </van-popup>
        <van-popup v-model="show1" position="bottom">
            <van-datetime-picker
                @confirm="confirm"
                @cancel = "cancel"
                v-model="currentDate"
                type="date"
            />
        </van-popup>
        <van-popup v-model="show2" position="bottom">
            <van-datetime-picker
                @confirm="confirm"
                @cancel = "cancel"
                v-model="currentDateTime"
                type="time"
            />
        </van-popup>
        <van-popup v-model="show3" position="bottom">
            <van-datetime-picker
                @confirm="confirm"
                @cancel = "cancel"
                v-model="currentDateTime"
                type="time"
            />
        </van-popup>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            list:[],
            active : 0,
            show:false,
            show1:false,
            show2:false,
            show3:false,
            currentDate: new Date(),
            currentDateTime:'12:00',
            model1:'',
            model2:'',
            model3:'',
            model4:'',
            index:0,
            history:[]
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num)
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/class'});//返回上一层
        },
        Time2Timestamps(Data){
            if(Data == undefined){
                return new Date().getTime();
            }
            return new Date(Data).getTime();
        },
        formatter(type,value){
            console.log(type,value)
        },
        confirm(value){
            if(this.index == 1){
                var time = publisFn.dateFormat(value.getTime());
                this.model1 = time.substring(0,10).replace(/\//g,'-');
                this.show=false;
            }else if(this.index == 2){
                var time = publisFn.dateFormat(value.getTime());
                this.model2 = time.substring(0,10).replace(/\//g,'-');
                this.show1=false;
            }else if(this.index == 3){
                this.model3 = value;
                this.show2=false;
            }else{
                this.model4 = value;
                this.show3=false;
            }
        },
        cancel(){
            this.show=false;
        },
        changeModel(num){
            this.index = num;
            if(num == 1){
                this.show=true;
            }else if(num == 2){
                this.show1=true;
            }else if(num == 3){
                this.show2=true;
            }else{
                this.show3=true;
            }
            
        },
        saveOrUpdateClassesTable(){//设置班级作息时间
            if(this.model1 == ''){
                this.$toast('请选择作息开始时间');
                return
            }
            if(this.model2 == ''){
                this.$toast('请选择作息结束时间');
                return
            }
            if(this.model3 == ''){
                this.$toast('请选择签到开始时间');
                return
            }
            if(this.model4 == ''){
                this.$toast('请选择签退开始时间');
                return
            }
            this.$toast.loading({
                mask: true,
                duration:0
            });
            var param = {
                classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                startTimeTable : this.model1,
                endTimeTable : this.model2,
                startSignTime : this.model3,
                endSignTime : this.model4
            };
            this.$http({url:this.$api.class.saveOrUpdateClassesTable},param).then(data => {
                this.$toast.clear();
                if(data.code == 0){
                    debugger
                    this.model1 = '';
                    this.model2 = '';
                    this.model3 = '';
                    this.model4 = '';
                    this.selectByClassesId();
                    this.$toast('创建成功');
                }else{
                    this.$toast(data.msg);
                }
            }).catch( res => {
                this.$toast.clear();
                this.$toast(res);
            })
        },
        saveOrUpdateClassScheduleRecord(id){
            this.$dialog.confirm({
                title: '提示',
                message: '确认删除么'
            }).then(() => {
                var param = {
                    id:id,
                    isdel:2
                };
                this.$http_json(this.$api.class.saveOrUpdateClassScheduleRecord,param).then(data => {
                    if(data.code == 0){
                        this.$toast('删除成功');
                        this.selectByClassesId();
                    }else{
                        this.$toast(data.msg);
                    }
                }).catch( res => {
                    this.$toast.clear();
                    this.$toast(res);
                })
            }).catch(() => {
            // on cancel
            });
        },
        selectByClassesId(){
            this.$toast.loading({
                mask: true,
                duration:0
            });
            var param = {
                classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId
            };
            this.$http({url:this.$api.class.selectByClassesId},param).then(data => {
                this.$toast.clear();
                if(data.code == 0){
                    this.history = data.data;
                }else{
                    this.$toast(data.msg);
                }
            }).catch( res => {
                this.$toast.clear();
                this.$toast(res);
            })
        },
        TimeDifference(time1,bool){//初始花时间 bool 为正表示+30分钟 false表示减半个小时
            var $timearr1 = time1.split(':');
            var $time = parseInt($timearr1[0])*60*60 + parseInt($timearr1[1])*60 + parseInt($timearr1[1]);//计算初始化时间总共多少秒
            if(bool){
                $time += 30*60
            }else{
                $time -= 30*60
            }
            var Hours = parseInt(num/3600);
            var Min = parseInt((num/3600 - Hours)*60);
            var Sends = num - Hours*3600 - Min*60;
            return Hours+'时'+ Min+'分' + Sends+'秒';





            //计算时间差
            var $time = Time2Timestamps(time1) - Time2Timestamps(time2);
        //计算出相差天数  
            var days=Math.floor($time/(24*3600*1000)); 
            //计算出小时数  
            var leave1=$time%(24*3600*1000)    //计算天数后剩余的毫秒数  
            var hours=Math.floor(leave1/(3600*1000));
            if(hours < 10){
                hours = '0' + hours ;
            }
            //计算相差分钟数  
            var leave2 = leave1%(3600*1000);        //计算小时数后剩余的毫秒数  
            var minutes = Math.floor(leave2/(60*1000)); 
            if(minutes < 10){
                minutes = '0' + minutes ;
            }
            //计算相差秒数  
            var leave3=leave2%(60*1000);      //计算分钟数后剩余的毫秒数  
            var seconds=Math.round(leave3/1000)  ;
            if(seconds < 10){
                seconds = '0' + seconds ;
            }
        //  alert("共记录"+days+"天 "+hours+"小时 "+minutes+" 分钟"+seconds+" 秒") 
            return "共记录"+hours+"小时 "+minutes+" 分钟"+seconds+" 秒"
        }
    },
    created() {
        this.selectByClassesId();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
    }
   
    .content{position: absolute;top: 0.84rem;right: 0.3rem;left: 0.3rem;bottom: 0;overflow-y: hidden;}
    .chooseBox{overflow: hidden;}
    .chooseDate p{font-size: .26rem;color: #333;margin: .6rem 0 .3rem 0;}
    .chooseInp{width: 3rem;height: .8rem;background: #f3f3f3;font-size: .26rem;line-height: .8rem;text-align: center;border-radius: 5px;border:none; }
    .lineDiv{width: .4rem;height: .02rem;background: #f3f3f3;margin: .39rem .25rem;}
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
    .list-for{position: absolute;top: 0.8rem;right: 0;left: 0;bottom: 0;height: calc(100vh - 2rem);overflow: auto}
    .list-item{overflow: hidden;height: 1.43rem;border-bottom: 1px solid #f2f2f2;}
    .dateTime{color: #adadad;}
    .item-right{line-height: 1.4rem;padding: 0 .3rem;}
</style>




